___CSS_LOADER_EXPORT___.push([module.id, `.menu-list {
  display: flex;
  flex-direction: column;
}
.menu-section {
  padding: 3rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.menu-heading {
  font-size: 3rem;
  position: relative;
}
.menu-heading::after {
  position: absolute;
  bottom: 0;
  left: 0;
  border-radius: 100vw;
  content: "";
  height: 5px;
  background-color: lightgreen;
}
.breakfast-heading::after {
  width: 250px;
}
.lunch-heading::after {
  width: 150px;
}
.menu-grid {
  padding: 3rem 0;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(3, 1fr);
  column-gap: 2rem;
  row-gap: 4rem;
}
