___CSS_LOADER_EXPORT___.push([module.id, `nav {
  border-bottom: 1px solid black;
  padding: 2rem;
}

ul {
  display: flex;
  list-style-type: none;
  justify-content: center;
  column-gap: 8rem;
}

li {
  font-size: 1.5rem;
  font-weight: 300;
  cursor: pointer;
}

li::after {
  display: flex;
  content: "";
  height: 3px;
  width: 0px;
  border-radius: 100vw;
  background-color: lightgreen;
  transition: all 150ms ease-in-out;
}

li:hover::after {
  width: 40px;
}

.active::after {
  display: flex;
  content: "";
  height: 3px;
  width: 40px;
  border-radius: 100vw;
  background-color: lightgreen;
  transition: all 150ms ease-in-out;
}`, "",{"version":3,"sources":["webpack://./src/Nav/nav.css"],"names":[],"mappings":"AAAA;EACE,8BAA8B;EAC9B,aAAa;AACf;;AAEA;EACE,aAAa;EACb,qBAAqB;EACrB,uBAAuB;EACvB,gBAAgB;AAClB;;AAEA;EACE,iBAAiB;EACjB,gBAAgB;EAChB,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,WAAW;EACX,WAAW;EACX,UAAU;EACV,oBAAoB;EACpB,4BAA4B;EAC5B,iCAAiC;AACnC;;AAEA;EACE,WAAW;AACb;;AAEA;EACE,aAAa;EACb,WAAW;EACX,WAAW;EACX,WAAW;EACX,oBAAoB;EACpB,4BAA4B;EAC5B,iCAAiC;AACnC","sourcesContent":["nav {\n  border-bottom: 1px solid black;\n  padding: 2rem;\n}\n\nul {\n  display: flex;\n  list-style-type: none;\n  justify-content: center;\n  column-gap: 8rem;\n}\n\nli {\n  font-size: 1.5rem;\n  font-weight: 300;\n  cursor: pointer;\n}\n\nli::after {\n  display: flex;\n  content: \"\";\n  height: 3px;\n  width: 0px;\n  border-radius: 100vw;\n  background-color: lightgreen;\n  transition: all 150ms ease-in-out;\n}\n\nli:hover::after {\n  width: 40px;\n}\n\n.active::after {\n  display: flex;\n  content: \"\";\n  height: 3px;\n  width: 40px;\n  border-radius: 100vw;\n  background-color: lightgreen;\n  transition: all 150ms ease-in-out;\n}"],"sourceRoot":""}]);
